@import "~@/assets/css/_variables.scss";

section {
  h2 {
    margin-bottom: 20px;
    font-size: $fs * 3;
    font-weight: bold;
  }
  p {
    font-size: $fs * 1.5;
  }
}
#banner {
  .banner-block {
    pointer-events: none;
    position: absolute;
    top: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-blend-mode: multiply;
      background-color: rgba($color: #000, $alpha: 0.4);

      background-image: url(../assets/image/中文3.jpg);
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center;
      background-attachment: fixed;
    }
  }
  #baner-text {
    h2 {
      color: #fff;
      font-weight: bold;
      letter-spacing: 8px;
      &:nth-child(1) {
        font-size: $fs * 2;
        margin-bottom: 30px;
      }
      &:nth-child(2) {
        font-size: $fs * 3;
        margin-bottom: 40px;
      }
      &:nth-child(3) {
        font-size: $fs * 1.2;
        padding: 10px 80px;
        background: linear-gradient(
          to right,
          rgba($color: $c-main, $alpha: 0) 0%,
          rgba($color: $c-main, $alpha: 0.8) 10%,
          rgba($color: $c-main, $alpha: 0.8) 90%,
          rgba($color: $c-main, $alpha: 0) 100%
        );
      }
    }
  }
}
#intro-DC {
  padding: 150px 0;
  background-color: rgba($color: #fff, $alpha: 0.8);
  background-blend-mode: lighten;
  background-image: url(../assets/image/home/dc-bg.jpg);
  background-position: center;
  background-size: cover;
  .link-dc {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    margin-top: 60px;
    padding: 20px 40px;
    font-size: $fs * 1.5;
    font-weight: bold;
    color: #fff;
    background: #5865f2;
    box-shadow: inset 0px -4px 0px 4px rgba($color: #000, $alpha: 0.16);

    transition: box-shadow 0.25s, background 0.25s;
    &:hover {
      background: #4a55cb;
      box-shadow: inset 0px 0px 0px 2px rgba($color: #000, $alpha: 0.16);
    }
  }
}
#feedback {
  padding: 150px 0;
  .img-feedback {
    width: 100%;
    border-radius: 8px;
    padding-bottom: 80%;
    background: url(../assets/image/home/feedback.jpg);
    background-position: center;
    background-size: cover;
  }
  .feedback-block {
    width: 80%;
    margin: 0 auto;
    .link-feedback {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      border-radius: 8px;
      margin-top: 60px;
      padding: 20px 40px;
      font-size: $fs * 1.5;
      font-weight: bold;
      color: #fff;
      background: $c-main;
      box-shadow: inset 0px -4px 0px 4px rgba($color: #000, $alpha: 0.16);

      transition: box-shadow 0.25s, background 0.25s;
      &:hover {
        background: $c-main-dark;
        box-shadow: inset 0px 0px 0px 2px rgba($color: #000, $alpha: 0.16);
      }
    }
  }
}
